import {
  URI_FETCH_NEW_PRE,
  URI_POST_TASK,
  URI_FETCH_EDIT_PRE,
  URI_FETCH_RENEW_PRE,
  URI_CHECK_APPID,
  URI_TASK_CONSUME,
  URI_APP_KEYWORD_IN_USE,
  URI_APP_KEYWORD_RANK
} from '@/constants'

import { Message } from 'element-ui'
import api from '@/fetch'
import _ from 'lodash'
import util from '@/utils'

const initState = () => ({
  advertiser_id: 0, // id for 
  zs_free: 0, // 10
  adForm: {
    title: '',
    download_url: '',
    click_notify_url: '',
    search_keyword: '', //   for 
    begin_time: '',  // 
    begin_time_time: '', // 
    end_time: '',  // 
    end_time_time: '', // 
    plan_count: '', // 
    plan_type: 2, //  1 2
    planlist: [], //  [{keyTime:'k1',key:''}],// [{keyTime:'k1',key:''},{keyTime:'k2',key:''}]
    appstore_type: '', //    
    appstore_cost: '0.000', // appstore
    platform: ' iPhone ', //  iPhone  iPad 
    zs_task: [],  // 
    is_new_activity: '',  // 10
    del_permission: '', // 10

    kw_flag_needed: '0', //  
    zs_task_needed: '0', //  
    remain_count: 0,  //  
    oldappid: '', // appid
    appid: '',
    isNew: false // app
  },
  ad_price: {  //   unit_price:    unit_price_level2:   zs_unit_price
    unit_price: 0,
    unit_price_level2: 0,
    zs_unit_price: 0
  },
  zs_rule: 1, // 10
  free_zs_num: 0, // 
  sum_cost: 0, // 
  available_balance: 0, // 
  recommKeywordList: [], // 
  appList: [] // 
})

// 
const state = initState()

//  Store  
const getters = {
  // TODO:
}

const types = {
  RESET_FORM: 'reset_form',
  RESET_FORM_BY_APP_SELECT: 'reset_form_by_app_select',
  SET_NEW_TASK_PRE: 'set_new_task_pre',
  UPDATE_REMAIN_COUNT: 'update_remain_count',
  UPDATE_ADFORM: 'update_adform',
  UPDATE_KEYWORDS_LIST: 'update_keywords_list',
  UPDATE_ZS_LISTS: 'update_zs_lists',
  REMOVE_ZS_ITEM: 'remove_zs_item',
  REMOVE_KEYWORDS_ITEM: 'remove_keywords_item',
  IS_NEW_APPID: 'is_new_appid',
  SET_AD_PRICE: 'set_ad_price',
  SYNC_APP_TYPE: 'sync_app_type',
  SYNC_SETTLE_INFO: 'sync_settle_info',
  RESET_APP_TYPE: 'reset_app_type',
  CHANGE_APP_TYPE: 'change_app_type',
  ADD_FREE_ZS: 'add_free_zs',
  SET_ZSTASK_NUM: 'set_zstask_num',
  CHANGE_ZS_NUM: 'change_zs_num',
  SYNC_APP_LIST: 'sync_app_list',
  SYNC_KEYWORD_RANK: 'sync_keyword_rank',
  SYNC_KEYWORD_LIST: 'sync_keyword_list',
  ADD_KEYWORDS_TO_PLANLIST: 'add_keywords_to_planlist'
}

// 
const mutations = {
  [types.RESET_FORM] (state) {
    state.adForm = {
      title: '',
      download_url: '',
      click_notify_url: '',
      search_keyword: '',
      begin_time: '',
      begin_time_time: new Date(2017, 1, 1, 0, 0),

      end_time: '',
      end_time_time: new Date(2017, 1, 1, 0, 0),
      plan_count: '',
      plan_type: 2,
      planlist: [],
      appstore_type: '', // 0  1
      appstore_cost: '0.000',
      platform: ' iPhone ',
      zs_task: [],
      is_new_activity: '',
      del_permission: '',

      kw_flag_needed: '0',
      zs_task_needed: '0',
      remain_count: 0,
      showKeyWordsMessage: false, // Message
      oldappid: '',
      appid: '',
      isHasBindMobile: false,
      isNew: false // app
    }
    state.sum_cost = 0 // 
    state.available_balance = 0 // 
    state.recommKeywordList = [] // 
    state.appList = [] // 
  },

  [types.RESET_FORM_BY_APP_SELECT] (state) {
    state.adForm = Object.assign({}, state.adForm, {
      begin_time: '',
      begin_time_time: new Date(2017, 1, 1, 0, 0),

      end_time: '',
      end_time_time: new Date(2017, 1, 1, 0, 0),
      plan_count: '',
      plan_type: 2,
      planlist: [],
      zs_task: [],
      remain_count: 0
    })
    state.sum_cost = 0 // 
  },

  [types.SYNC_APP_LIST] (state, appList) {
    state.appList = appList
  },

  [types.SYNC_KEYWORD_RANK] (state, payload) {
    let transPlanlist = state.adForm.planlist[payload.index]
    state.adForm.planlist[payload.index] = Object.assign({}, transPlanlist, {...payload.data})
    state.adForm.planlist = [...state.adForm.planlist]
  },

  [types.SYNC_KEYWORD_LIST] (state, keywordList) {
    state.recommKeywordList = keywordList
  },

  [types.ADD_KEYWORDS_TO_PLANLIST] (state, keywords) {
    console.log(types.ADD_KEYWORDS_TO_PLANLIST, keywords)
    let planlist = state.adForm.planlist
    keywords.forEach((obj, index) => {
      // index  keyTime  keyTime
      let keyTime = Date.now() + index
      planlist.push({
        key: obj.keyword,
        num: '',
        priority: obj.priority,
        rank: obj.rank,
        keyTime
      })
    })
  },

  [types.RESET_APP_TYPE] (state) {
    state.adForm.appstore_type = ''
  },

  [types.CHANGE_APP_TYPE] (state, payload) {
    state.adForm.appstore_type = payload
  },

  [types.SYNC_APP_TYPE] (state, payload) {
    state.adForm.appstore_type = payload.app_type
    state.adForm.appstore_cost = payload.app_price
  },

  [types.SYNC_SETTLE_INFO] (state, payload) {
    // checkAppId 
    if (payload.total_consume) {
      state.sum_cost = payload.total_consume
    }
    // checkAppId  payload.available_balance 
    if (payload.available_balance) {
      state.available_balance = payload.available_balance
    }
  },

  [types.SET_NEW_TASK_PRE] (state, payload) {
    state.advertiser_id = payload.advertiser_id
    state.zs_free = payload.zs_free
    state.ad_price = payload.ad_price
  },

  [types.SET_AD_PRICE] (state, adPrice) { //  
    state.ad_price = adPrice
  },

  [types.UPDATE_ADFORM] (state, form) {
    state.adForm = {...form, isNew: false, isHasBindMobile: false}
  },

  [types.UPDATE_REMAIN_COUNT] (state, remain) {
    state.adForm.remain_count = remain
  },

  [types.UPDATE_KEYWORDS_LIST] (state, keywordsItem) {
    state.adForm.planlist.push(keywordsItem)
  },

  [types.UPDATE_ZS_LISTS] (state, zsItem) {
    state.adForm.zs_task.push(zsItem)
  },

  [types.ADD_FREE_ZS] (state, items) {
    state.adForm.zs_task = items
  },

  [types.SET_ZSTASK_NUM] (state, payload) {
    state.zs_rule = payload.zs_rule
    state.free_zs_num = payload.free_zs_num
  },

  [types.REMOVE_ZS_ITEM] (state, zsItemKey) {
    var _temporary = []
    var _key = zsItemKey

    for (var i = 0; i < state.adForm.zs_task.length; i++) {
      var _t = state.adForm.zs_task[i]
      if (i === (state.adForm.zs_task.length - 2)) {
        _t.can_delete = true
      }
      if (_t.key !== _key) {
        _temporary.push(_t)
      }
    }
    state.adForm.zs_task = _temporary
  },

  [types.REMOVE_KEYWORDS_ITEM] (state, keywordsItemKey) {
    var _temporary = []
    for (var i = 0; i < state.adForm.planlist.length; i++) {
      var _t = state.adForm.planlist[i]
      if (_t.keyTime !== keywordsItemKey) {
        _temporary.push(_t)
      }
    }
    state.adForm.planlist = _temporary
  },

  [types.IS_NEW_APPID] (state, {isNew, isHasBindMobile}) {
    state.adForm.isNew = isNew
    state.adForm.isHasBindMobile = isHasBindMobile
  },

  [types.CHANGE_ZS_NUM] (state, type) {
    if (state.adForm.zs_task.length === 0) return
    if (type === 1) { // >= 5000
      let item = {
        the_day: '1',
        free: true,
        money: 0,
        key: state.adForm.zs_task[0].key,
        can_delete: false
      }
      state.adForm.zs_task[0] = item
    } else {
      let canDelete
      if (state.adForm.zs_task.length > 1) {
        canDelete = false
      } else {
        canDelete = true
      }
      let item = {
        the_day: '1',
        free: false,
        money: 1,
        key: state.adForm.zs_task[0].key,
        can_delete: canDelete
      }
      state.adForm.zs_task[0] = item
    }
  }
}

//    commit()mutations Store dispatch Actions
const actions = {
  resetFormByAppSelect ({commit}) {
    commit(types.RESET_FORM_BY_APP_SELECT)
  },
  resetForm ({commit}) {
    commit(types.RESET_FORM)
  },
  fetchPreNew ({dispatch, commit}, {appid, fromPathName}) {
    let uri = URI_FETCH_NEW_PRE
    if (appid) {
      uri = `${uri}?appid=${appid}`
    }
    return api(uri, {method: 'GET'})
      .then(res => res && res.payload)
      .then(payload => {
        if (payload.navbar) dispatch('updateNavbar', payload, { root: true })
        // 
        commit(types.RESET_FORM)
        // 
        commit(types.SET_NEW_TASK_PRE, payload)
        // 
        commit(types.SET_AD_PRICE, payload.ad_price)
        // 
        commit(types.SET_ZSTASK_NUM, payload)
        // 
        commit(types.SYNC_APP_LIST, payload.app_list)
        // 
        commit(types.SYNC_SETTLE_INFO, payload)
        // 
        fromPathName !== 'app_list' &&
        payload.shopping_cart_kw_list &&
        commit(types.ADD_KEYWORDS_TO_PLANLIST, payload.shopping_cart_kw_list)
      })
  },

  fetchPreRenew ({dispatch, commit, state}, taskId) {
    let path = URI_FETCH_RENEW_PRE + taskId
    return api(path, {method: 'GET'})
      .then(res => res && res.payload)
      .then(payload => {
        // 
        if (payload.navbar) dispatch('updateNavbar', payload, { root: true })
        commit(types.SET_AD_PRICE, payload.ad_price)
        // 
        commit(types.SET_ZSTASK_NUM, payload)
        if (payload.navbar) dispatch('updateNavbar', payload, { root: true })
        commit(types.SYNC_SETTLE_INFO, payload)
        dispatch('formatPreInfoForEditAndRenew', payload)
        var isNewActivity = state.adForm.is_new_activity
        if (state.zs_rule === 1 && isNewActivity === 0) {
          let items = []
          for (let i = 1; i <= state.free_zs_num; i++) {
            let zsInfo = {
              the_day: i.toString(),
              free: true,
              money: 0,
              key: Date.now()
            }
            items.push(zsInfo)
          }
          commit(types.ADD_FREE_ZS, items)
        }
        return payload
      })
      .catch(err => {
        if (err.payload && err.payload.code === 404) {
          return {notFound: true}
        }
      })
  },

  fetchPreEdit ({dispatch, commit}, taskId) {
    let path = URI_FETCH_EDIT_PRE + taskId
    return api(path, {method: 'GET'})
      .then(res => res && res.payload)
      .then(payload => {
        commit(types.SET_AD_PRICE, payload.ad_price)
        // 
        commit(types.SET_ZSTASK_NUM, payload)
        commit(types.SYNC_SETTLE_INFO, payload)
        // 
        commit(types.SYNC_APP_LIST, payload.app_list)
        if (payload.navbar) dispatch('updateNavbar', payload, { root: true })
        dispatch('formatPreInfoForEditAndRenew', payload)
        return payload
      }).catch(err => {
        if (err.payload && err.payload.code === 404) {
          return {notFound: true}
        }
      })
  },

  formatPreInfoForEditAndRenew ({commit}, payload) {
    let info = {
      advertiser_id: payload.advertiser_id,
      ad_price: payload.task.ad_price,
      zs_free: payload.zs_free
    }
    // 
    // 
    if (payload.price_change === 1) {
      payload.task.appstore_type = payload.task.appstore_type === 1 ? 0 : 1
    }
    commit(types.SET_NEW_TASK_PRE, info)
    commit(types.SET_AD_PRICE, payload.task.ad_price)
    _.forEach(payload.task.zs_task, function (n) {
      n.univalent === 0 ? n.free = true : n.free = false
    })
    //  
    if (!!payload.task.search_keyword && payload.task.planlist.length === 0) {
      payload.task.planlist.push({
        key: payload.task.search_keyword,
        num: '100',
        keyTime: Date.now()
      })
    }

    for (var i = 0; i < payload.task.planlist.length; i++) {
      payload.task.planlist[i].keyTime = Date.now() + i
    }
    parseInt(payload.task.platform) === 1 ? payload.task.platform = ' iPhone '
      : (parseInt(payload.task.platform) === 2 ? payload.task.platform = ' iPad '
      : payload.task.platform = '')
    payload.task.plan_type = 2
    payload.task.remain_count = 0

    var hh = util.formatTime(payload.task.begin_time).substr(11, 2)
    var mm = util.formatTime(payload.task.begin_time).substr(14, 2)
    payload.task.begin_time_time = new Date(2016, 9, 10, hh, mm, 0)

    hh = util.formatTime(payload.task.end_time).substr(11, 2)
    mm = util.formatTime(payload.task.end_time).substr(14, 2)
    payload.task.end_time_time = new Date(2016, 9, 10, hh, mm, 0)

    payload.task.begin_time = new Date(payload.task.begin_time * 1000)
    payload.task.end_time = new Date(payload.task.end_time * 1000)

    for (let i = 0; i < payload.task.zs_task.length; i++) {
      let item = payload.task.zs_task[i]
      payload.task.zs_task[i].key = Date.now() + i
      item.can_delete = false
      payload.task.zs_task[payload.task.zs_task.length - 1].can_delete = true
    }
    commit(types.UPDATE_ADFORM, payload.task)
  },

  postForm ({commit}, config) {
    let path = URI_POST_TASK
    config.routeName === 'dash_ad_edit' ? path = '/v2/api/task/' + config.params : (config.routeName === 'dash_ad_renew' ? path = '/v2/api/task/copy/' + config.params : path = '/v2/api/task')
    return api(path, {method: 'post', special: true, body: config.postData})
      .then(res => res && res.payload)
      .then(payload => {
        return payload
      })
  },

   // 
  rePlanAnalysis ({dispatch, commit, state}) {
    var planlist = state.adForm.planlist
    planlist.forEach(function (p) {
      p.num = ''
    })
    dispatch('planAnalysis')
  },

  //  
  planAnalysis ({commit, state}) {
    var sum = 0
    var remain = state.adForm.plan_count
    var planlist = state.adForm.planlist
    planlist.forEach(function (p) {
      if (typeof (p.num) !== 'undefined' && !isNaN(parseInt(p.num))) {
        sum += parseInt(p.num)
      }
    })
    if (sum >= remain) {
      remain = 0
    } else {
      remain = remain - sum
    }
    commit(types.UPDATE_REMAIN_COUNT, remain)
  },

  addKeyWordList ({commit, state}) {
    //  
    var count = Math.floor(state.adForm.plan_count / 100)
    var iniItem = {
      key: '',
      num: '',
      priority: '-',
      rank: '-',
      keyTime: Date.now()
    }
    if (state.advertiser_id === 45) {
      // 45 
      commit(types.UPDATE_KEYWORDS_LIST, iniItem)
    } else {
      //  < count &&  < 100
      if (state.adForm.planlist.length < count) {
        // 
        var total = state.adForm.planlist.length
        if (total >= 100) {
          Message({
            message: '100',
            iconClass: 'qk-warning'
          })
          return
        }
        commit(types.UPDATE_KEYWORDS_LIST, iniItem)
      } else {
        if (state.showKeyWordsMessage) {
          return
        } else {
          state.showKeyWordsMessage = true
          // 
          Message({
            message: '' + count + ',100',
            iconClass: 'qk-warning',
            onClose: function (m) {
              state.showKeyWordsMessage = false
            }
          })
        }
      }
    }
  },

  removeKeyWordsItem ({commit}, itemKeyTime) {
    commit(types.REMOVE_KEYWORDS_ITEM, itemKeyTime)
  },

  // 
  addZSLists ({dispatch, commit, state}) {
    var canDelete = false
    var freeFlg = false
    var money = 1
    if (state.adForm.plan_count !== '' && state.adForm.plan_count >= 5000 && state.zs_free === 1 && state.zs_rule === 0) {
      freeFlg = true
      money = 0
    }

    for (var i = 0; i < state.adForm.zs_task.length; i++) {
      state.adForm.zs_task[i].can_delete = false
    }
    if (state.adForm.zs_task.length === 0) {
      canDelete = true
      var item1 = {
        the_day: '1',
        free: freeFlg,
        money: money,
        key: Date.now(),
        can_delete: canDelete
      }
      commit(types.UPDATE_ZS_LISTS, item1)
    } else {
      var item2 = {
        the_day: '0',
        free: false,
        money: money,
        key: Date.now(),
        can_delete: true
      }
      commit(types.UPDATE_ZS_LISTS, item2)
    }
    // 
    dispatch('countSumCost', state.adForm)
  },

  // 
  changeZsNum ({commit, state}, type) {
    if (state.zs_rule !== 0 || state.zs_free !== 1) return
    commit(types.CHANGE_ZS_NUM, type)
  },

  // 
  removeZSItem ({dispatch, commit, state}, itemKey) {
    commit(types.REMOVE_ZS_ITEM, itemKey)
    dispatch('countSumCost', state.adForm)
  },

  // 
  countSumCost ({commit}, {appid, plan_count, zs_task}) {
    let days = zs_task.length
    let needed = days > 0 ? 1 : 0
    let planCount = {plan_count}.plan_count
    // 
    if (!appid || !planCount || /[^\d]/g.test(appid + planCount)) {
      return
    }
    let uri = URI_TASK_CONSUME + `?appid=${appid}&plan_count=${planCount}&zs_task_needed=${needed}&zs_days=${days}`
    api(uri).then((res) => {
      commit(types.SYNC_SETTLE_INFO, res.payload)
    })
  },
  changeAppstoreType ({commit}) {
    let appstoreType = state.adForm.appstore_type === 1 ? 0 : 1
    commit(types.CHANGE_APP_TYPE, appstoreType)
  },

  // appid
  checkAppId ({dispatch, commit, state}, appid) {
    if (appid === state.adForm.oldappid) {
      return
    } else {
      // 
      if (/[^\d]/g.test(appid)) {
        commit(types.RESET_APP_TYPE)
        return
      }
      let uri = `${URI_CHECK_APPID}?appid=${appid}`
      return api(uri).then((res) => {
        let payload = res.payload
        let isNew = false
        if (payload.code === 20004) {
          isNew = true
        }
        if (payload.code === 20001 || payload.code === 20002) {
          Message({
            message: payload.msg,
            iconClass: 'qk-warning'
          })
          commit(types.RESET_APP_TYPE)
          return
        }
        commit(types.IS_NEW_APPID, {isNew, isHasBindMobile: payload.mobile})
        commit(types.SYNC_APP_TYPE, payload)
        if (state.zs_rule === 1) { // checkAPPid
          if (payload.code === 20004 && payload.mobile) {
            state.adForm.zs_task = []
          } else { // 
            let items = []
            for (let i = 1; i <= state.free_zs_num; i++) {
              let zsInfo = {
                the_day: i.toString(),
                free: true,
                money: 0,
                key: Date.now()
              }
              items.push(zsInfo)
            }
            commit(types.ADD_FREE_ZS, items)
          }
        }

        if (state.adForm.plan_count !== '') {
          dispatch('countSumCost', state.adForm)
        }
        return {isNew}
      })
    }
  },
  getKeywordList ({commit, state}) {
    let uri = `${URI_APP_KEYWORD_IN_USE}?appid=${state.adForm.appid}`
    return api(uri).then(res => {
      console.log('getKeywordList res: ', res)
      commit(types.SYNC_KEYWORD_LIST, res.payload.data)
    })
  },
  getKeywordRank ({commit, state}, index) {
    let params = {appid: state.adForm.appid, keyword: state.adForm.planlist[index].key}
    console.log('getKeywordRank params: ', params)
    if (!params.keyword) {
      // keyword
      commit(types.SYNC_KEYWORD_RANK, {
        data: {priority: '-', rank: '-'},
        index
      })
      return
    }
    api(URI_APP_KEYWORD_RANK, {
      method: 'POST',
      body: params
    }).then(res => {
      console.log('getKeywordRank res: ', res)
      commit(types.SYNC_KEYWORD_RANK, {
        data: res.payload.data,
        index
      })
    })
  },
  addKeywordsToPlanList ({commit}, keywords) {
    commit(types.ADD_KEYWORDS_TO_PLANLIST, keywords)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



